<template>
  <div class="page-article">
    <div class="d-flex py-3 px-2 border-bottom">
      <div class="iconfont iconback text-blue"></div>
      <strong class="flex-1 text-blue">
        {{model.title}}
      </strong>
      <div class="text-grey fs-xs">
        2020-08-15
      </div>
    </div>
    <!-- 内容 -->
    <div v-html="model.body" class="px-3 body fs-lg"></div>
    <!-- 关联内容 -->
    <div class="px-3 border-top py-2">
      <div class="d-flex ai-center">
        <i class="iconfont iconmenu-1"></i>
        <strong class="text-blue fs-lg ml-1">相关资讯</strong>
      </div>
      <div class="pt-2">
        <router-link class="py-1 "
        tag="div" 
        :to="`/articles/${item._id}`"
        v-for="item in model.related" 
        :key="item._id">{{ item.title }}</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      model: {}
    }
  },
  props: {
    id: {required: true}
  },
  // 监听，id变化重新执行fetch方法
  watch: {
    id: 'fetch',
  },
  methods: {
    async fetch(){
      const res = await this.$http.get(`/articles/${this.id}`)
      this.model = res.data
      console.log(res.data);
      
    }
  },
  created() {
    this.fetch()
  },
}
</script>

<style lang="scss">
  .page-article {
    .iconback {
      font-size: 1.5rem;
    }
    .body {
      img {
        max-width: 100%;
        height: auto;
      }
      iframe {
        width: 100%;
        height: auto;
      }
    }
  }
</style>